  font-size: 20px;
  margin: 0;
}

.accordion li h3::before {
  content: "";
  background-image: url("../img/svg/down-arrow.svg");
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translate(-50%, -50%);
}

.accordion li p {
  display: none;
}

.accordion .openFaq h3 {
  border-bottom: 1px dotted #ccc;
  padding-bottom: 10px;
}

.accordion .openFaq p {
  display: block;
  font-size: 14px;
  animation: showContent 1s linear forwards;
  transition: all .3s;
}

.accordion .openFaq h3::before {
  top: 7px;
  transform: rotate(180deg);
}

@keyframes showContent {
  0% {
    opacity: 0;
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 1;
  }
}

